html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

:root {
  --blue: #2380c3;
  --dark-blue: #141f61;
  --light-blue: #4baceb;
  --yellow: #e1da20;
}

body {
  background-color: var(--blue);
  justify-content: center;
  align-items: center;
  display: flex;
}

main {
  max-width: 100%;
  width: 900px;
  margin: 0 auto;
  padding: 1rem;
}

form {
  gap: 2rem;
  display: grid;
}

h1 {
  text-align: center;
  color: var(--dark-blue);
  font-family: sans-serif;
  font-size: 2rem;
}

input[type="text"] {
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  background-color: var(--dark-blue);
  color: var(--yellow);
  border: 2px solid var(--light-blue);
  border-radius: .3rem;
  font-size: 2rem;
  display: block;
}

button {
  background-color: var(--light-blue);
  border: 2px solid var(--dark-blue);
  color: var(--dark-blue);
  width: 163px;
  border-radius: .3rem;
  margin: 0 auto;
  padding: 1rem;
  font-size: 1.5rem;
}

button:hover {
  background-color: var(--dark-blue);
  color: var(--light-blue);
}

li {
  text-transform: capitalize;
  color: var(--yellow);
  line-height: 1.5rem;
}

.letters {
  flex: 1;
  gap: .25rem;
  display: flex;
}

.solution {
  margin-top: 1rem;
}

.other-solutions {
  background-color: var(--dark-blue);
  color: var(--light-blue);
  border: 2px solid var(--light-blue);
  border-radius: .3rem;
  padding: 1rem;
  display: none;
}

.active {
  display: block;
}

.inputs-wrapper {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

@media screen and (max-width: 600px) {
  .letters {
    grid-template-columns: repeat(6, 1fr);
    display: grid;
  }
}

